<template>
  <div class="modify-password">
    <div class="content">
      <div class="modify-pwd">
        <div class="tip">
          
          <span>6-15</span>
        </div>
        <div class="input-pwd">
          <input placeholder="" type="password" name id="old-psw" v-model.trim="oldPsw">
          <input placeholder="" type="password" name id="new-psw" v-model.trim="newPsw">
          <input
            placeholder=""
            type="password"
            name
            id="new-psw-again"
            v-model.trim="newPswAgain"
            @keyup.enter="updatePassword"
          >
        </div>
        <div class="btn">
          <div class="confirm-btn" @click="updatePassword"></div>
          <div class="cancel-btn" @click="$router.push({name:'person_security_set'})"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { i_person_header } from "@/assets/img.js";
import { mapActions, mapState } from "vuex";
import { warnTip ,encode} from "@/utils/js/tool.js";

export default {
  data() {
    return {
      oldPsw: "",
      newPsw: "",
      newPswAgain: ""
    };
  },
  computed: {
    ...mapState('account',{
      account:state=>state.user.account
    })
  },
  methods: {
    ...mapActions("account", ["post_updatePassword"]),
    updatePassword() {
      if (this.newPsw.length < 6) {
        warnTip("6");
        return;
      }
      if (this.newPsw !== this.newPswAgain) {
        warnTip("");
        return;
      }
      this.post_updatePassword({
        newPsw:encode(this.newPsw,this.account.email),
        oldPsw:encode(this.oldPsw,this.account.email)
      })
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./css/modifyPassword.less");
</style>
