<template>
  <div class="login">
    <div class="bbs_logo">
      <img :src="brand" alt="lij">
    </div>
    <div class="login-wrapper">
      <div class="login-main">
        <div class="login-content">
          <form action class="login-form">
            <p class="title">BBS</p>
            <div class="login-account">
              <label for="input-email">/</label>
              <input
                type="email"
                name="email"
                id="input-email"
                v-model.trim="account"
                placeholder=""
              >
            </div>
            <div class="login-password">
              <label for="input-password">
                
                <a href="/account/forgetPassword" class="forget-password"></a>
              </label>
              <input
                type="password"
                name="password"
                id="input-password"
                v-model.trim="password"
                placeholder=""
                @keyup.enter="loginWithPassword"
              >
            </div>
            <div class="login-btn" @click="loginWithPassword"></div>
            <div class="checkout-register">
              BBS
              <span class="click-register">
                <a href="/account/register"></a>
              </span>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { i_account } from "@/assets/img.js";
import { warnTip, isPhoneOrMail } from "@/utils/js/tool";
import { mapActions, mapState } from 'vuex';
import { setTimeout } from 'timers';
export default {
  name:'Login',
  data() {
    return {
      brand: i_account.bbs_brand,
      account: "",
      password: ""
    };
  },
  computed:{
    ...mapState('account',['user'])
  },
  methods: {
    ...mapActions('account',['post_loginWithPassword']),
    loginWithPassword() {
      // 
      if (!isPhoneOrMail(this.account)) {
        warnTip("");
        return;
      }
      if (this.password.length < 6) {
        warnTip("");
        return
      }
      this.post_loginWithPassword({password:this.password,account: this.account});
    }
  },
  watch:{
    user:function(newVal,oldVal){
      newVal?setTimeout(()=> {this.$router.push({name:'home'})},1000):''
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./css/login.less");
</style>
