<template>
    <div class="type">
        <!--  -->
        <div class="part-1">
            <div class="left"><span></span></div>
            <div class="right">
                <input type="text" name="" id="search-items" placeholder="/">
                <label for="search-items"><img :src="i_search" alt=""></label>
            </div>
        </div>
        <!--  -->
        <div class="part-2">
            <ul class="clearfix">
                <li :class="{'li-actived':val.actived}" @click="val.actived=!val.actived" v-for="(val,index) in types" :key="index" >{{val.name}}</li>
            </ul>
            <div class="clear-all"><img @click="clearAll" :src="i_clear_all" alt="" title=""></div>
        </div>
    </div>
</template>

<script>
import { i_type } from "@/assets/img.js";
import { mapState } from 'vuex';
export default {
    data() {
        return {
            i_search:i_type.search,
            i_clear_all:i_type.clearAll
        }
    },
    computed: {
        ...mapState('secondHand',['types'])
    },
    methods: {
        clearAll(){
            this.types.forEach(d=>d.actived = false)
        }
    },
}
</script>

<style lang="less" scoped>
@import url('./css/type.less');
</style>

