/**
 * 3
 * @export
 * @param {*} data 
 */
export async function api_get_getMostHotNews(){
    try {
        const r = await axios.get("/news/rest/mostHot")
        let {code,msg,data} = r.data
        if(code === 'success'){
            return data
         }else if(code === 'fail'){
             console.log(msg)
         }
    } catch (error) {
        console.log(error)
    }
}

