/**
 * /
 * @export
 * @param {*} newsId 
 * @returns
 */
export async function api_get_like(newsId){
    try {
        const r = await axios.get("/user/like/"+newsId)
        let {code,msg,data} = r.data
        if(code === 'success'){
            successTip(msg)
            return data
         }else if(code === 'fail'){
             console.log(msg)
             warnTip(msg)
         }
    } catch (error) {
        console.log(error)
    }
}

