/**
 *
 *
 * @export
 * @param {*} account
 * @param {*} showExisted 
 */
export async function api_get_account_existed(account,showExisted = true){
    try {
        const r = await axios.get('/rest/account/existed/'+account)
        let {code,msg}  = r.data
        if(code === 'fail'&&showExisted){
            warnTip(msg)
            return true
        }else if(code === 'success'&&!showExisted){
            warnTip(msg)
        }
    } catch (error) {
        console.log(error)
    }
}
