<template>
  <div class="edit">
    <div class="wang" id="wang"></div>
  </div>
</template>
<script>
import Wangeditor from "wangeditor";
import { generateUUID,successTip,errTip } from "@/utils/js/tool.js";
import axios from 'axios'

const win = window;
export default {
  name: "editor",
  data() {
    return {
      editor: ""
    };
  },
  methods: {
     initEditor() {
      this.editor = new Wangeditor("#wang");
      this.editor.customConfig.menus = [
        "head", // 
        "bold", // 
        "fontSize", // 
        "fontName", // 
        "italic", // 
        "underline", // 
        "strikeThrough", // 
        "foreColor", // 
        "backColor", // 
        "link", // 
        "list", // 
        "justify", // 
        "quote", // 
        "image", // 
        "table", // 
        "code", // 
        "undo", // 
        "redo" // 
      ];
      this.editor.customConfig.customUploadImg =function(files, insert) {
        let newForm = new win.FormData();
        newForm.append("file", files[0]);
        const r =axios.post("/private/upload/release", newForm).then(d=>{
          console.log(d)
          insert(d.data.data[0])
        });
      };
      this.editor.create();
    }
  },
  mounted() {
    this.initEditor();
    win.editor = this.editor;
    console.log(this.editor);
  }
};
</script>


